import "core-js/modules/es.number.constructor.js";
import elementResizeDetectorMaker from 'element-resize-detector';
import Swiper from 'swiper';
import page from './Page.vue';
import page2 from './Page2.vue';
export default {
  components: {
    page: page,
    page2: page2
  },
  props: {
    menuId: [String, Number]
  },
  data: function data() {
    return {
      scalseNum: 1,
      pagesHeight: 1080,
      componentList: ['page', 'page2'],
      show: true,
      pWidth: 1920
    };
  },
  mounted: function mounted() {
    var _this = this;
    this.$nextTick(function () {
      elementResizeDetectorMaker().listenTo(document.getElementById('pages'), function (element) {
        // _this.pWidth = element.clientWidth
        _this.resize_window(element.clientWidth);
      });
    });
    this.bannerPoint();
  },
  methods: {
    resize_window: function resize_window(width) {
      var scalseNum = Number(width / 1920);
      this.scalseNum = scalseNum;
      this.pagesHeight = 1080 * scalseNum;
    },
    bannerPoint: function bannerPoint() {
      new Swiper(".swiper-container", {
        loop: true,
        initialSlide: 0,
        //设定初始化时slide的索引
        autoplay: {
          delay: 2000,
          // 1秒切换一次
          disableOnInteraction: false
        },
        updateOnImagesReady: true,
        slidesPerView: 1,
        //设置slider容器能够同时显示的slides数量
        watchOverflow: true,
        //因为仅有1个slide，swiper无效
        //spaceBetween : '10%',按container的百分比

        observer: true,
        //在 Swiper 的上启用动态检查器(Mutation Observer)，如果你更改swiper 的样式（隐藏/显示）或修改其子元素（添加/删除幻灯片），Swiper 会更新（重新初始化）并触发 observerUpdate 事件。
        observeParents: true,
        observeSlideChildren: true
      });
    }
  }
};